.projects {
    padding: 50px 0 50px 0;   
    font-family: 'Cormorant Garamond', serif; 
}

/* Left part with projects clickable icons */
.listProjects{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.listCard{
    display: flex;
    flex-direction: column;

    height: min-content;
    width:min-content;
    margin: 0 30px;
    padding: 10px;

    border: 2px solid rgb(50, 50, 50);
    border-radius: 200px;
    background-color: rgba(214, 214, 214, 0.353);
}


/* Project image preview */
.preview img {
    width: 50vw;
    height: auto ;

    border-radius: 25px;
    box-shadow: 1px 1px 5px;
    border: 4px solid rgb(203, 203, 203);
    
    opacity: 0.95;

    transition: 0.2s ease;
}

.preview:hover img {
    opacity: 0.7;
}


/* Project image's overlay with text*/
.content_img{
    display: flex;
    position: relative;
    justify-content: center;
    align-items: center;
}

.content_img .text_img{
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: absolute;

    height: 100%;
    width: 100%;

    border-radius: 25px;
    background: rgb(215, 215, 215);
    color: rgb(0, 0, 0);
    font-family: 'Alegreya Sans', sans-serif;

    opacity: 0;
    
    -webkit-transition: visibility 0s, opacity 0.2s ease-in-out; 
    transition: visibility 0s, opacity 0.2s ease-in-out;
}

.content_img:hover .text_img{
    opacity: 0.85; 
}

.content_img .text_img p{
    padding: 0 3vw;

    font-size: 1.65vw;
    line-height: 1.7vw;
    text-align: justify;
}


/* Buttons on the right part */
.project_right{
    display: flex;
    flex-direction: column;
    align-items: center;

    width: 25vw;
    height: auto;
}

.project_right a{
    position: relative;

    height: min-content;
    width: 15vw;
    padding: 5px;
    margin: 20px 0;

    border: 3px solid rgb(18, 18, 18);
    border-radius: 1px;
    overflow: hidden;

    background-color: none;
    
    color: rgb(20, 20, 20);

    text-align: center;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 1.8vw;
    letter-spacing: 0.2vw;
    text-decoration: none;
    
}

.project_right a:hover{
    color: rgb(249, 251, 253);
    transition: all 0.4s;
}

.project_right a:before {
    content: "";

    width: 0%;
    height: 100%;

    position: absolute;
    top: 0;
    left: 0;

    background: linear-gradient(0.25turn, rgb(10, 10, 10), rgb(56, 56, 56));
    z-index: -1;
    
    transition: all 0.3s;
}

.project_right a:hover:before{
    width: 100%;
}

/* List of tools used (react, js...) */
.usedTools {
    display: flex;
    flex-direction: column;
}

.usedTools h2{
    text-align: center;
}

.listTools{
    display: flex;
    justify-content: space-around;
    align-items: center;
    
    font-size: 4vw;
}

.listTools svg{
    margin: 0 1.7vw
}