.landing{
    display: flex;
    flex-direction:row;
    justify-content: center;
    align-items: center;

    height: 100vh;
    margin: 0;
    padding:0 10vw ;

    background:linear-gradient(0.75turn, rgb(5, 5, 5), rgb(75, 75, 75));

    z-index: -1;
}

.landing .content{
    display: flex;
    flex-direction: column;
}
.landing h1{
    width: 60vw;
    padding: 20px 0 ;
    
    font-family: 'Cormorant Garamond', serif;
    font-size: 3vw;
    font-weight: 600;

    color: rgb(239, 239, 239);
}

.landing a{
    position: relative;
    height: min-content;

    width: 15vw;
    padding: 5px;
    margin: 40px 0;

    border: 3px solid rgb(244, 244, 244);
    border-radius: 1px;
    
    overflow: hidden;

    background-color: none;
    
    color: rgb(249, 251, 253);

    text-align: center;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: 600;
    font-size: 1.8vw;
    letter-spacing: 0.2vw;
    text-decoration: none;

    z-index: 0;
}

.landing a:hover{
    color: rgb(20, 20, 20);
    transition: all 0.4s;
    cursor: pointer;
}

.landing a:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 0%;
    height: 100%;

    background: linear-gradient(rgb(245, 245, 245), rgb(171, 171, 171));

    z-index: -1;
    
    transition: all 0.3s;
}

.landing a:hover:before{
    width: 100%;   
}

.landing .drawing{
    height:auto;
    width:22vw;

    margin: 0 5vw;

    clip-path: circle();
    
    opacity: 0.95;
}


