.navbar{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex-wrap: wrap;

    height: auto;

    position: sticky;
    top:0;
    background: linear-gradient(0.22turn, rgb(4, 8, 32), rgb(130, 130, 130));
    
    z-index: 1;
}

.navbar a {
    padding: 3px 4vw;
    
    font-family: 'Alegreya Sans', sans-serif;
    font-size: 1.5rem;
    font-weight: 400;
    text-decoration: none;

    color:rgb(255, 255, 255);
}

.navbar a:hover {
    transform: scale(1.02);
    color:rgb(229, 229, 229);
    cursor: pointer;
}
