.aboutMe_one {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    
    height:fit-content;
    
    background: url(../../img/bg3.jpg) no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;

    font-family: 'Cormorant Garamond', serif;
    color: antiquewhite;
    
    opacity: 0.95;
}

.aboutMe_two {
    display: flex;
    justify-content: flex-start;
    align-items: center; 

    height:fit-content;

    background: url(../../img/bg1_1.jpg) no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;

    font-family: 'Cormorant Garamond', serif;
    color: antiquewhite;

    opacity: 1;
}

.aboutMe_three {
    display: flex;
    justify-content: flex-end;
    align-items: center; 

    height:fit-content;

    background: url(../../img/bg2.jpg) no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;

    clip-path: polygon(0% 0%,100% 0%,100% 90%,0% 100%);
    padding: 0 0 50px 0;

    font-family: 'Cormorant Garamond', serif;
    color: antiquewhite;

    opacity: 0.95;
}

.aboutMe_one .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 40vh;
    padding: 0 15px;

    text-shadow: black 2px 4px 5px;
}

.aboutMe_two .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 40vh;
    padding: 0 15px;
}

.aboutMe_three .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    height: 40vh;
    padding: 0 15px;
    
    text-shadow: black 2px 2px 2px;
}

.text {
    width: 30vw;
}