.contact{
    display: flex;
    flex-direction: column;  

    padding: 80px 0 50px 0; 

    background: linear-gradient(0.75turn, rgb(26, 26, 26), rgb(0, 0, 0));
    clip-path:polygon(0% 10%, 100% 0%,100% 100%,0% 100%);
    
    font-family: 'Cormorant Garamond', serif;
    color: antiquewhite;
}

/* List of Icons */
.listIcons{
    display: flex;
    justify-content: space-around;
}

/* Individual Icons */
.listIcons a{
    color: rgb(188, 167, 167);
}
.listIcons a:hover{
    color: rgb(195, 195, 195);
}
.listIcons h1:active{
    transform:scale(0.9);
}
.listIcons h1{
    font-size: 7vh;
}

/* Image and text */
.group{
    display: flex;
    justify-content: flex-start;
}

#message {
    display:flex;
    justify-content: center;
    align-items: center;
}

.contact .drawing{
    height:180px;
    width:auto;

    margin: 0 50px;

    clip-path: circle();
}

.contact p{
    width:30vw;

    font-size: 1.4rem;
    font-style:italic ;
}

.contact span {
    display: flex;
    justify-content: right;

    margin: 10px 0;

    font-size: 1.1rem;
}